import * as React from "react"
import cx from "classnames"

import theme from "../../constants/theme"
import { ISharedData } from "../../data/shared"

import { patternArrowsBL, patternArrowsTR } from "../../assets/graphics"
import { Button } from "../../components"

/**
 * CallToActionSection
 */
type Props = {
  sharedData: ISharedData
}

export default function CallToActionSection({ sharedData }: Props) {
  return (
    <section
      className={cx(
        "flex justify-center",
        "px-20 md:px-40 lg:px-60 xl:px-80",
        "py-60 md:py-60 lg:py-80 xl:py-120",
        "bg-bg2"
      )}
      css={{
        backgroundImage: `url(${patternArrowsBL}), url(${patternArrowsTR})`,
        backgroundPosition: `bottom left, top right`,
        backgroundRepeat: "no-repeat",
        backgroundSize: `80px, 80px`,
        [theme.mq(theme.screens.md)]: {
          backgroundSize: `120px, 120px`,
        },
      }}>
      <div className={cx("lg:w-10/12", "flex flex-col")}>
        {title()}
        <div className={cx("grid grid-cols-1 md:grid-cols-2")}>
          {textGroup1()}
          {textGroup1(true)}
          {textGroup2(true)}
          {textGroup2()}
        </div>
      </div>
    </section>
  )

  function title() {
    return (
      <div className={cx("flex", "pb-40")}>
        <div className={cx("lg:w-6/12")}>
          {sharedData.callToActionSection.subTitle && (
            <p className={cx("text-sm3 text-accent1 uppercase tracking-wide_md", "w-full")}>
              {sharedData.callToActionSection.subTitle}
            </p>
          )}
          <h2
            className={cx(
              "font-medium text-lg2 md:text-xl lg:text-xl2 xl:text-xl3 text-text1",
              "resetLeading"
            )}>
            {sharedData.callToActionSection.title}
          </h2>
        </div>
      </div>
    )
  }

  function textGroup1(hide: boolean = false) {
    return (
      <div className={cx(hide && "hidden lg:flex lg:opacity-0", "flex", "lg:pr-40")}>
        <div className={cx("pb-40")}>
          <p className={cx("text-sm3 md:md2 lg:text-md3 text-text2")}>
            {sharedData.callToActionSection.text1}
          </p>
        </div>
      </div>
    )
  }

  function textGroup2(hide: boolean = false) {
    return (
      <div className={cx(hide && "hidden lg:flex lg:opacity-0", "flex", "lg:pr-40")}>
        <div className={cx("flex flex-col", "gap-40 md:gap-60")}>
          <p className={cx("text-sm3 md:md2 lg:text-md3 text-text2")}>
            {sharedData.callToActionSection.text2}
          </p>

          <div className={cx(hide && "hidden")}>
            <Button
              to={sharedData.callToActionSection.moreButton.route}
              title={sharedData.callToActionSection.moreButton.title}
              size="xl"
              frameColor={theme.colors.accent1}
              frameHoverOpacity={0.2}
              flexible={true}
            />
          </div>
        </div>
      </div>
    )
  }
}
